     background-image: url(/img/JessSmokePortfolio.jpg);
}

#contact-work-container{
     background-image: url(/img/registrationFormPortfolio.png);
   }


   



.work:hover {
    -webkit-animation: zoomin 2s ;
    -webkit-animation-fill-mode: forwards;
}



@-webkit-keyframes zoomin {
    0% {
        -webkit-transform: scale(1);
    }
    100% {
        -webkit-transform: scale(1.05);
    }

}